
.login-form{
  position: relative;
  top: -10px;
  left: -10px;
  max-width: 100%;
  height: 110px;
  padding: 20px;
}
input{
  width: 200px;
  height: 20px;
}
.span2{
  position: relative;
  left: 230px;
  max-width: 100px;
  color: red
}
.g-form-btn{
  width: 35px;
  padding: 0 20px 0 20px;
  position: absolute;
  left: 300px;
  top: 130px;
  box-shadow: 1px 1px 6px #080808;
  border-radius: 10px;
  background-color: #969696;
  cursor: pointer;
}
.g-form-error{
  position: absolute;
  left: 250px;
  top: 45px;
  color: red
}
.g-form-btn:hover{
  color: red
}



