
*{
	margin: 0;
	padding: 0
}
.aoo-wrap{
	width: 100%;
	height: 100%
}
.app-header{
	width: 100%;
	height: 90px;
	background-color: #454545;
}
img{
	width: 80px;
	margin-top: 0.5%; 
	margin-left: 8%;
}
ul{
	position: relative;
	left: -200px;
	top: -58px;
	color: #fff;
	float: right;
}
li{
	margin: 10px;
	display: inline;
	font-size: 20px;
}
li:hover{
	cursor:pointer
}
.nav-pile{
	width: 30px;
	height: 25px;
}
.app-footer{
	width: 100%;
	text-align: center;
	position: absolute;
	top:1030px;
	background-color: #d4d4d4
}
.app-content{
	background-color: #e8e8e8;
	height: 950px
}



